import { useState, useEffect } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
  Card,
  Form,
  Input,
  Button,
  Checkbox,
  Alert,
  Space,
  Divider,
  Typography,
} from "antd";
import {
  UserOutlined,
  LockOutlined,
  GoogleOutlined,
  GithubOutlined,
  WindowsOutlined,
} from "@ant-design/icons";
import { message } from "antd";
import { loginUser, registerUser } from "../../services/securityService";
import securityService from "../../services/securityService";
import Loading from "../Common/Loading";
import "./Auth.css";

const { Title, Text } = Typography;

const Login = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const dispatch = useDispatch();
  const { isAuthenticated } = useSelector((state) => state.auth);
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);
  const [isRegister, setIsRegister] = useState(false);
  const [error, setError] = useState(null);
  const [ssoLoading, setSsoLoading] = useState(null);

  // L·∫•y returnUrl t·ª´ query params
  const returnUrl = searchParams.get("returnUrl") || "/";

  // Redirect if already authenticated
  useEffect(() => {
    const token =
      localStorage.getItem("authToken") || localStorage.getItem("token");
    if (token || isAuthenticated) {
      // N·∫øu c√≥ returnUrl, redirect v·ªÅ ƒë√≥, n·∫øu kh√¥ng th√¨ v·ªÅ home
      navigate(returnUrl !== "/" ? returnUrl : "/");
    }
  }, [isAuthenticated, navigate, returnUrl]);

  const handleSubmit = async (values) => {
    setLoading(true);
    setError(null);

    try {
      if (isRegister) {
        // Register
        await registerUser(values.email, values.password, "user");
        message.success("ƒêƒÉng k√Ω th√†nh c√¥ng! Vui l√≤ng ƒëƒÉng nh·∫≠p.");
        setIsRegister(false);
        form.resetFields();
      } else {
        // Login
        const result = await loginUser(values.email, values.password);

        if (result.requiresMFA) {
          // MFA required - redirect to MFA page
          message.info("Vui l√≤ng nh·∫≠p m√£ MFA");
          navigate("/security", {
            state: { requiresMFA: true, email: values.email },
          });
        } else if (result?.token) {
          // Login success
          localStorage.setItem("authToken", result.token);
          localStorage.setItem("token", result.token);

          // Store sessionId if provided
          if (result.sessionId) {
            localStorage.setItem("sessionId", result.sessionId);
          }

          // Update Redux store
          dispatch({
            type: "LOGIN_SUCCESS",
            payload: {
              user: result.user,
              sessionId: result.sessionId,
              serviceAccount: result.serviceAccount,
            },
          });

          message.success("ƒêƒÉng nh·∫≠p th√†nh c√¥ng!");

          // Redirect v·ªÅ returnUrl n·∫øu c√≥, n·∫øu kh√¥ng th√¨ v·ªÅ home
          const redirectTo =
            returnUrl && returnUrl !== "/login" ? returnUrl : "/";
          navigate(redirectTo);
        }
      }
    } catch (err) {
      setError(err.message || "ƒêƒÉng nh·∫≠p th·∫•t b·∫°i");
      message.error(err.message || "ƒêƒÉng nh·∫≠p th·∫•t b·∫°i");
    } finally {
      setLoading(false);
    }
  };

  const handleSSOLogin = async (provider) => {
    setSsoLoading(provider);
    try {
      const authData = await securityService.getSSOAuthUrl(provider);
      if (authData?.authUrl) {
        window.location.href = authData.authUrl;
      } else {
        message.error(`Kh√¥ng th·ªÉ l·∫•y URL x√°c th·ª±c t·ª´ ${provider}`);
        setSsoLoading(null);
      }
    } catch (error) {
      message.error(`L·ªói ƒëƒÉng nh·∫≠p ${provider}: ${error.message}`);
      setSsoLoading(null);
    }
  };

  const ssoProviders = [
    {
      id: "google",
      name: "Google",
      icon: <GoogleOutlined />,
      color: "#4285F4",
    },
    {
      id: "github",
      name: "GitHub",
      icon: <GithubOutlined />,
      color: "#24292e",
    },
    {
      id: "microsoft",
      name: "Microsoft",
      icon: <WindowsOutlined />,
      color: "#00A1F1",
    },
  ];

  if (loading && !isRegister) {
    return <Loading fullScreen text="ƒêang ƒëƒÉng nh·∫≠p..." />;
  }

  return (
    <div className="auth-container">
      <div className="auth-wrapper">
        <Card className="auth-card">
          <div className="auth-header">
            <Title level={2} className="auth-title">
              {isRegister ? "üìù ƒêƒÉng k√Ω" : "üîê ƒêƒÉng nh·∫≠p"}
            </Title>
            <Text type="secondary">
              {isRegister
                ? "T·∫°o t√†i kho·∫£n m·ªõi ƒë·ªÉ s·ª≠ d·ª•ng h·ªá th·ªëng"
                : "Ch√†o m·ª´ng tr·ªü l·∫°i MIA.vn Integration"}
            </Text>
          </div>

          {error && (
            <Alert
              message={error}
              type="error"
              showIcon
              closable
              onClose={() => setError(null)}
              style={{ marginBottom: 24 }}
            />
          )}

          <Form
            form={form}
            name="auth"
            onFinish={handleSubmit}
            autoComplete="off"
            layout="vertical"
            size="large"
          >
            <Form.Item
              name="email"
              label="Email"
              rules={[
                { required: true, message: "Vui l√≤ng nh·∫≠p email!" },
                { type: "email", message: "Email kh√¥ng h·ª£p l·ªá!" },
              ]}
            >
              <Input
                prefix={<UserOutlined />}
                placeholder="your@email.com"
                autoComplete="email"
              />
            </Form.Item>

            <Form.Item
              name="password"
              label="M·∫≠t kh·∫©u"
              rules={[
                { required: true, message: "Vui l√≤ng nh·∫≠p m·∫≠t kh·∫©u!" },
                { min: 6, message: "M·∫≠t kh·∫©u ph·∫£i c√≥ √≠t nh·∫•t 6 k√Ω t·ª±!" },
              ]}
            >
              <Input.Password
                prefix={<LockOutlined />}
                placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                autoComplete={isRegister ? "new-password" : "current-password"}
              />
            </Form.Item>

            {isRegister && (
              <Form.Item
                name="confirmPassword"
                label="X√°c nh·∫≠n m·∫≠t kh·∫©u"
                dependencies={["password"]}
                rules={[
                  { required: true, message: "Vui l√≤ng x√°c nh·∫≠n m·∫≠t kh·∫©u!" },
                  ({ getFieldValue }) => ({
                    validator(_, value) {
                      if (!value || getFieldValue("password") === value) {
                        return Promise.resolve();
                      }
                      return Promise.reject(
                        new Error("M·∫≠t kh·∫©u x√°c nh·∫≠n kh√¥ng kh·ªõp!"),
                      );
                    },
                  }),
                ]}
              >
                <Input.Password
                  prefix={<LockOutlined />}
                  placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                  autoComplete="new-password"
                />
              </Form.Item>
            )}

            {!isRegister && (
              <Form.Item>
                <div className="auth-options">
                  <Checkbox>Ghi nh·ªõ ƒëƒÉng nh·∫≠p</Checkbox>
                  <a href="#forgot" className="auth-link">
                    Qu√™n m·∫≠t kh·∫©u?
                  </a>
                </div>
              </Form.Item>
            )}

            <Form.Item>
              <Button
                type="primary"
                htmlType="submit"
                block
                loading={loading}
                size="large"
              >
                {isRegister ? "ƒêƒÉng k√Ω" : "ƒêƒÉng nh·∫≠p"}
              </Button>
            </Form.Item>
          </Form>

          <Divider>Ho·∫∑c</Divider>

          <Space direction="vertical" size="middle" style={{ width: "100%" }}>
            {ssoProviders.map((provider) => (
              <Button
                key={provider.id}
                icon={provider.icon}
                block
                size="large"
                loading={ssoLoading === provider.id}
                onClick={() => handleSSOLogin(provider.id)}
                style={{
                  borderColor: provider.color,
                  color: provider.color,
                }}
              >
                ƒêƒÉng nh·∫≠p v·ªõi {provider.name}
              </Button>
            ))}
          </Space>

          <div className="auth-footer">
            <Text type="secondary">
              {isRegister ? "ƒê√£ c√≥ t√†i kho·∫£n? " : "Ch∆∞a c√≥ t√†i kho·∫£n? "}
              <a
                href="#"
                onClick={(e) => {
                  e.preventDefault();
                  setIsRegister(!isRegister);
                  form.resetFields();
                  setError(null);
                }}
                className="auth-link"
              >
                {isRegister ? "ƒêƒÉng nh·∫≠p" : "ƒêƒÉng k√Ω ngay"}
              </a>
            </Text>
          </div>
        </Card>
      </div>
    </div>
  );
};

export default Login;
